@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins','sans-serif';
}
.body{
    max-width: 1000px;
    height: 72vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.header{
    font-size: 30px;
    display: flex;
    flex-direction: column;
}
.middle{
    width: 30rem;
    height: 33rem;
    display: flex;
    flex-direction: column;
    background: rgba(9, 98, 182, 0.06);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
    border-radius: 30px;
}
.h1{
    font-size: 30px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
    opacity: .5;
    letter-spacing: 3px;
    margin: 1% auto;
}
label{
    font-size: 20px;
    text-align: left;
    margin-left: 10%;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}
.inputB{
    margin: 1% auto;
    display: flex;
    justify-content: center;
    align-items: center;
}
.inputB .input1{
    border: none;
    outline: none;
    color: white;
    background: transparent;
    border-bottom: 1px solid rgba(255, 255, 255, 0.89);
}
::placeholder {
    color: whitesmoke;
    opacity: .7;
  }
.input{
    width: 80%;
    margin: .35% auto;
    border: none;
    outline: none;
    color: white;
    background: transparent;
    border-bottom: 2px solid rgba(255, 255, 255, 0.89);
    opacity: .6;
}

.btn{
    width: 50%;
    margin: 3% auto;
    color: rgb(24, 61, 94);
    font-size: 15px;
    opacity: .6;
    background: rgba(255, 255, 255, 0.06);
    padding: 10px 30px;
    border: none;
    outline: none;
    border-radius: 20px;
    text-shadow: 2px 2px 5px rgba(255, 255, 255, 0.2);
    box-shadow: 3px 3px 5px rgba(31,38,135,0.37);
    border-bottom: 1px solid rgba(25, 255, 255, 0.3);
    border-top: 1px solid rgba(25, 255, 255, 0.3);
    cursor: pointer;
}