@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

*{
  margin: 0;
  padding: 0;
  font-family: "Quicksand";
  color: #333;
}

body{
  background: linear-gradient(to right, rgb(16, 140, 172), rgb(45, 172, 41));
}
.navbar{
  padding: 15px;
  display: flex;
  align-items: center;
  max-width: 1000px;
  margin: 2% auto;
  border-bottom: 2px solid #ee0c0c;
}
.navbar h1{
  margin-right: 45%;
}
.navbar a {
  padding: 6px;
  text-decoration: none;
}
.navbar a:hover{
  color: #ee0c0c;
}
.content{
  margin: -30px auto;
  padding: 20px;
  max-width: 1000px;
}
h1{
  cursor: default;
}
.contentinhome{
  display: flex;
  padding: 80px auto;
  justify-content: space-between;
  color: white;
}
.para, .para1, .para2{
  margin-top: 5%;
  color: white;
}
.hov:hover{
  color: #ee0c0c;
}
.img{
  margin-top: -2.8%;
  height: 500px
}

.notification-wrapper{
  position: fixed;
  top: 10px;
  right: 10px;
  width: 300px;
}

.notification-item{
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 15px;
  animation: SlideLeft 0.2s;
  animation-fill-mode: forwards;
  width: 300px;
}

.notification-item .message {
  margin: 0;
  padding: 5px; 
}

.notification-item .bar{
  height: 5px;
}
.notification-item.success .bar {
  background-color: #052006;
}

.notification-item.error .bar {
  background-color: red;
}

@keyframes SlideLeft {
  0% {
    margin-left: 120%;
  }
  100% {
    margin-left: 0%;
  }
}

@keyframes SlideRight {
  0% {
    margin-left: 0%;
  }
  100% {
    margin-left: 120%;
  }
}

.notification-item.exit {
  animation: SlideRight 0.3s;
  animation-fill-mode: forwards;
}